




































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue'; // @ is an alias to /src

@Component({
  components: {
    CButton,
  },
})
export default class CPermutation extends Vue {
  // Data
  private dataButtonText: string = '順列の探索';
  private dataText: string = '';
  private dataNumber: number = 0;
  private dataPermutations: string[] = [];
  private dataTextMaxLength: number = 7;

  // Methods
  private listenInputText($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value.slice(0, this.dataTextMaxLength);
    this.dataNumber = this.dataText.length;
  }
  private listenInputNumber($event: Event): void {
    let number: number | string = (<HTMLInputElement>(<any>$event).target).value;
    if (number.match(/^([0-9]|[1-9][0-9]+)$/g)) {
      number = Number(number);
      if (this.dataText === '') {
        this.dataNumber = 0;
      } else if (0 <= number && number <= this.dataText.length) {
        this.dataNumber = number;
      } else {
        this.dataNumber = this.dataText.length;
      }
    } else {
      this.dataNumber = this.dataText.length;
    }
  }
  private generatePermutation(): void {
    this.dataPermutations.length = 0;
    if (0 < this.dataNumber) {
      this.generatePermutationRecursively(this.dataText, this.dataNumber, '');
    }
  }
  private generatePermutationRecursively(textFragment: string, number: number, permutationFragment: string): void {
    let permutationFragmentAdded: string;
    for (let i: number = 0; i < textFragment.length; i++) {
      permutationFragmentAdded = permutationFragment + textFragment.charAt(i);
      if (permutationFragmentAdded.length < number) {
        let textFragmentSliced: string = textFragment.slice(0, i) + textFragment.slice(i + 1);
        this.generatePermutationRecursively(textFragmentSliced, number, permutationFragmentAdded);
      } else {
        if (this.dataPermutations.indexOf(permutationFragmentAdded) < 0) {
          this.dataPermutations.splice(this.dataPermutations.length, 0, permutationFragmentAdded);
        }
      }
    }
  }
}
